*{
  padding: 0;
  margin: 0;
  scrollbar-width: none;
}

header{
  align-items: center;
  position: fixed;
  top: 0;
  width: 100vw;
  height: 18vh;
  display: flex;
  z-index: 4;
}

.header_component strong{
  line-height: 18vh;
  color: white;
  font-size: 1.8em;
} 

#hour{
  width: 25vw;
  float: left;
}

#tittle{
  width: 50vw;
  float: center;
  font-size: 2.8em;
}

#location{
  width: 25vw;
  float: right;
}

#background{
  top: 0;
  position: fixed;
  z-index: -10;
}


.cards{
  position: relative;
  height: 60vh;
  width: 100vw;
  background: transparent;
  margin-top: 25vh;
  z-index: 2;
}

.card_day{
  display: inline-block;
  position: relative;
  height: 60vh;
  width: 18vw;
  margin: 1vh 1vw 3vh 1vw;
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 5px;
  color: white;
}

.card_day strong{
  font-size: 2.2em;
}

.weather_hourly div{
  display: inline-block;
  margin: 10px;
  margin-top: 30px;
}

.weather_hourly img{
  width: 20px;
  height: 20px;
}